













import { Component, Vue } from 'vue-property-decorator'
import DateRangePicker from '@/components/DateRangePicker.vue'

@Component({
  components: {
    DateRangePicker
  },
})
export default class Home extends Vue {
  theme = {
    primary: '#46C3A3',
    secondary: '#2D3E50',
    ternary: '#93A0BD',
    light: '#ffffff',
    border: '#e6e6e6',
    dark: '#000000',
    hovers: {
      day: '#CCC',
      range: '#e6e6e6'
    }
  }
  checkUpdate(values) {
    this.$router.push({ query: Object.assign({}, this.$route.query, {
      to: values.to,
      from: values.from,
      panel: values.panel
    }) })
  }
}
